
import React from 'react';
import Breadcrumb from 'antd/lib/breadcrumb';

export default () => {
    return (
        <Breadcrumb style={{ margin: '12px 0' }}>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>
    )
}


